import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/:catchAll(.*)',
    component: () => import('../views/frontend/Index.vue'),
  },
  {
    path: '/',
    component: () => import('../views/frontend/Layout.vue'),
    children:[
      {
        path: 'index',
        component: () => import('../views/frontend/Index.vue'),
      },
      {
        path: 'about',
        component: () => import('../views/frontend/About.vue'),
      },
      {
        path: '/course',
        component: () => import('../views/frontend/CourseOverview.vue'),
      },
      {
        path: '/course/:courseId',
        component: () => import('../views/frontend/CourseView.vue'),
      },
      {
        path: '/cartFlow',
        component: () => import('../views/frontend/CartFlow.vue'),
      },
      {
        path: '/cartForm',
        component: () => import('../views/frontend/CartForm.vue'),
      },
      {
        path: '/cartFinish',
        component: () => import('../views/frontend/CartFinish.vue'),
      },
      {
        path: '/faq',
        component: () => import('../views/frontend/Faq.vue'),
      },
    ]
  },
  // {
  //   path: '/checkout/:orderId',
  //   component: () => import('../views/frontend/Checkout.vue'),
  // },
  {
    path: '/login',
    component: () => import('../views/backend/Login.vue'),
  },
  {
    path: '/admin',
    component: () => import('../views/backend/Dashboard.vue'),
    children:[
      {
        path: 'products',
        component: () => import('../views/backend/Products.vue'),
      },
      {
        path: 'orders',
        component: () => import('../views/backend/Orders.vue'),
      },
    ]
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router
